
@mixin color-shade-palette ($color_scheme, $target)
{
    @each $color, $value in $color_scheme {
        @if ($target == 'bg') {
            @include bg-variant(".bg-#{$color}", $value);
            .#{$target}-#{"" + $color} {

                @if $enable-contrast-colors {
                    &.contrast {
                        color: contrast($value);
                    }
                }

                @if $enable-blend-colors {
                    &.blend {
                        color: blend($value);
                    }

                    &.blend-soft {
                        color: blend_soft($value);
                    }

                    &.blend-hard {
                        color: blend_hard($value);
                    }
                }

                @if $enable-complement-colors {
                    &.complement {
                        color: complement_color($value);
                    }
                }

                @if $enable-invert-colors {
                    &.invert {
                        color: invert_color($value);
                    }
                }

            }
        } @else {
            @include text-emphasis-variant(".text-#{$color}", $value);

            .#{$target}-#{"" + $color} {

                @if $enable-contrast-colors {
                    &.contrast {
                        background-color: contrast($value);
                    }
                }

                @if $enable-blend-colors {
                    &.blend {
                        background-color: blend($value);
                    }

                    &.blend-soft {
                        background-color: blend_soft($value);
                    }

                    &.blend-hard {
                        background-color: blend_hard($value);
                    }
                }

                @if $enable-complement-colors {
                    &.complement {
                        background-color: complement_color($value);
                    }
                }

                @if $enable-invert-colors {
                    &.invert {
                        background-color: invert_color($value);
                    }
                }


            }
        }

        @each $weight, $percentage in $palette {

            $current_color : $value;

            @if $weight < 500 {
                $current_color : mix(white, $value, $percentage);
            } @else if $weight > 500 {
                $current_color : mix(black, $value, $percentage);
            }

            @if ($target == 'bg') {
                @include bg-variant(".bg-#{$color}-#{$weight}", $current_color);

                .#{$target}-#{"" + $color}-#{$weight} {

                    @if $enable-contrast-colors {
                        &.contrast {
                            color: contrast($current_color);
                        }
                    }

                    @if $enable-blend-colors {
                        &.blend {
                            color: blend($current_color);
                        }

                        &.blend-soft {
                            color: blend_soft($current_color);
                        }

                        &.blend-hard {
                            color: blend_hard($current_color);
                        }
                    }

                    @if $enable-complement-colors {
                        &.complement {
                            color: complement_color($current_color);
                        }
                    }

                    @if $enable-invert-colors {
                        &.invert {
                            color: invert_color($current_color);
                        }
                    }

                }

            } @else {
                @include text-emphasis-variant(".text-#{$color}-#{$weight}", $current_color);

                .#{$target}-#{"" + $color}-#{$weight} {

                    @if $enable-contrast-colors {
                        &.contrast {
                            background-color: contrast($current_color);
                        }
                    }

                    @if $enable-blend-colors {
                        &.blend {
                            background-color: blend($current_color);
                        }

                        &.blend-soft {
                            background-color: blend_soft($current_color);
                        }

                        &.blend-hard {
                            background-color: blend_hard($current_color);
                        }
                    }

                    @if $enable-complement-colors {
                        &.complement {
                            background-color: complement_color($current_color);
                        }
                    }

                    @if $enable-invert-colors {
                        &.invert {
                            background-color: invert_color($current_color);
                        }
                    }
                }
            }
        }
    }
}
